import React, {Component} from 'react';
import {
  ConfigProvider,
  Icon,
  Form,
  Button,
  Radio,
  DatePicker,
  Input,
  Upload,
  InputNumber,
  Divider,
  Modal,
  Select,
  Tag,
  TimePicker
} from "antd";
import {connect} from "react-redux";
import {actionCreators} from '../store';
import zhCN from 'antd/es/locale/zh_CN';
import moment from "moment";
import 'moment/locale/zh-cn';
import "./index.scss";
import MultiSelectByGroup from '../../../../common/components/SelectGonghao/MultiSelectSider';

const {Option} = Select;

class EditMessage extends Component {

  UNSAFE_componentWillMount() {
    this.props.getAllCodes();
    this.props.getAllGroup();
    let query = this.props.history.location.query;
    query && this.props.initKefuMessageInfo(query);
  }

  render() {
    const {kefuMessageInfo, content, changeContentValue, changeMessageValue, uploadImg, submitMessage, allCodes, selectGonghao, deleteGonghao, history, openPreviewModal, closePreviewModal, openid, changeOpenId, previewModal, preview, allGroup, groupInputVisible, groupValue, showGroupInput, handleGroupInputChange, handleGroupInputConfirm, handleClose, cancelSelectGroup} = this.props;
    let isUploadImg, isTextUploadImg, FormItemWrapper;
    if (content.picurl) {
      isTextUploadImg = <img style={{width: "100px"}} src={content.picurl} alt=""/>
    } else {
      isTextUploadImg = <Button><Icon type="upload"/> 上传图片 </Button>
    }
    if (kefuMessageInfo.img) {
      isUploadImg = <img style={{width: "100px"}} src={kefuMessageInfo.img} alt=""/>
    } else {
      isUploadImg = <Button><Icon type="upload"/> 上传图片 </Button>
    }
    if (kefuMessageInfo.type === 1) {
      FormItemWrapper = <Form.Item label="文本消息" className="text-message">
        <Input.TextArea autoSize={{minRows: 6}} value={content.description} placeholder="请输入文本消息"
                        onChange={e => changeContentValue(e, "description")}/>
      </Form.Item>
    } else if (kefuMessageInfo.type === 0) {
      FormItemWrapper = <div>
        <Form.Item label="图片封面">
          <Upload name="imageFile" action="/reply/upload" onChange={(info) => {
            uploadImg(kefuMessageInfo, info)
          }} showUploadList={false}>
            {isTextUploadImg}
          </Upload>
        </Form.Item>
        <Form.Item label="图文标题">
          <Input placeholder="请输入图文标题" value={content.title} onChange={e => changeContentValue(e, "title")}/>
        </Form.Item>
        <Form.Item label="图文描述" className="text-message">
          <Input.TextArea autoSize={{minRows: 6}} value={content.description} placeholder="请输入图文描述"
                          onChange={e => changeContentValue(e, "description")}/>
        </Form.Item>
        <Form.Item label="跳转链接">
          <Input placeholder="请输入跳转链接" value={content.url} onChange={e => changeContentValue(e, "url")}/>
        </Form.Item>
      </div>
    } else if (kefuMessageInfo.type === 2) {
      FormItemWrapper =
        <Form.Item label="图片消息">
          <Upload name="imageFile" action="/reply/upload" onChange={(info) => {
            uploadImg(kefuMessageInfo, info)
          }} showUploadList={false}>
            {isUploadImg}
          </Upload>
        </Form.Item>;
    }
    const formItemLayout = {
      labelCol: {
        sm: {span: 4}
      },
      wrapperCol: {
        sm: {span: 20, offset: 0}
      },
    };
    let Channel;
    if (kefuMessageInfo.channel === 1) {
      Channel = <><Form.Item label="延时时间">
        <InputNumber value={kefuMessageInfo.delay} min={1} onChange={value => changeMessageValue(value, "delay")}/>
        <Select value={kefuMessageInfo.isHour} onChange={value => changeMessageValue(value, "isHour")}
                style={{width: 80}}>
          <Option value={0}>分钟</Option>
          <Option value={1}>小时</Option>
        </Select>
      </Form.Item>
        <Form.Item label="互动类型">
          <Radio.Group value={kefuMessageInfo.action_type}
                       onChange={e => changeMessageValue(e.target.value, "action_type")}>
            <Radio value={1}>用户关注</Radio>
            <Radio value={2}>用户互动</Radio>
          </Radio.Group>
        </Form.Item>
      </>
    } else if (kefuMessageInfo.channel === 2) {
      Channel =
        <Form.Item label="定时时间">
          <DatePicker showTime placeholder="选择时间" value={moment(kefuMessageInfo.timing_time)}
                      onChange={(value, dateString) => changeMessageValue(dateString, 'timing_time')}/>
        </Form.Item>
    } else if (kefuMessageInfo.channel === 3) {
      Channel =
        <Form.Item label="选择时间">
          <TimePicker placeholder="选择时间" value={moment(kefuMessageInfo.daily_time_show)}
                      onChange={(value, dateString) => changeMessageValue(value, 'daily_time_show')}/>
        </Form.Item>
    }
    return (
      <ConfigProvider locale={zhCN}>

        <div className="container">
          <MultiSelectByGroup selectGonghao={selectGonghao}  codes={kefuMessageInfo.codes} content={<div className="container edit-message">
            <div className="kefu-form fl">
              <Form {...formItemLayout}>
                <Form.Item label="公号列表">
                  <div style={{width: "180%"}}>
                   {allCodes.map(item => kefuMessageInfo.codes.indexOf(item.code) > -1 && <Tag key={item._id} className="cursor gonghao-tag" closable onClose={() => deleteGonghao(kefuMessageInfo.codes, item.code)}>{item.nick_name}</Tag>)}
                  </div>
                </Form.Item>
                <Form.Item label="消息名称">
                  <Input value={kefuMessageInfo.remarks} placeholder="请输入消息名称,(仅供备注，不会发送给粉丝)"
                         onChange={e => changeMessageValue(e.target.value, "remarks")}/>
                </Form.Item>
                <Form.Item label="消息分组">
                  <Tag color={kefuMessageInfo.group !== "未分组" ? "#1890ff" : ""} closable={kefuMessageInfo.group !== "未分组"}
                       onClose={cancelSelectGroup}
                       style={kefuMessageInfo.group !== "未分组" ? {} : {background: '#fff', borderStyle: 'dashed'}}>
                    {kefuMessageInfo.group}
                  </Tag>
                </Form.Item>
                <Form.Item label="全部分组">
                  {groupInputVisible && (
                      <Input
                          ref={input => this.input = input}
                          type="text"
                          size="small"
                          style={{width: 78, marginRight: 10}}
                          value={groupValue}
                          onChange={handleGroupInputChange}
                          onBlur={handleGroupInputConfirm}
                          onPressEnter={handleGroupInputConfirm}
                      />
                  )}
                  {!groupInputVisible && (
                      <Tag className="cursor" onClick={() => showGroupInput(this)}
                           style={{background: '#fff', borderStyle: 'dashed'}}>
                        <Icon type="plus"/> New Tag
                      </Tag>
                  )}
                  {allGroup.map((item, index) => <Tag color="#1890ff" key={item._id} className="cursor" closable onClose={e => handleClose(e, item._id, index)} onClick={e => changeMessageValue(item.group, "group")}>
                    {item.group}
                  </Tag>)}
                </Form.Item>
                <Divider style={{width: "205%", marginLeft: -50}}/>
                <h2 className="block-title">设置客服消息</h2>
                <Form.Item>
                  <Radio.Group buttonStyle="solid" value={kefuMessageInfo.type} onChange={e => changeMessageValue(e.target.value, "type")}>
                    <Radio.Button value={0}>图文消息</Radio.Button>
                    <Radio.Button value={1}>文本消息</Radio.Button>
                  </Radio.Group>
                </Form.Item>
                {FormItemWrapper}
                <Divider style={{width: "175%", marginLeft: -50}}/>
                <h2 className="block-title">选择群发对象</h2>
                <Form.Item label="群发粉丝">
                  <Radio.Group value={kefuMessageInfo.sex} onChange={e => changeMessageValue(e.target.value, "sex")}>
                    <Radio value="all">全部粉丝</Radio>
                    <br/>
                    <Radio value="1">仅男性粉丝</Radio>
                    <Radio value="2">仅女性粉丝</Radio>
                    <Radio value="0">仅未知粉丝</Radio>
                  </Radio.Group>
                </Form.Item>
                <Divider style={{width: "175%", marginLeft: -50}}/>
                <h2 className="block-title">设置群发方式</h2>
                <Form.Item label="发送方式">
                  <Radio.Group value={kefuMessageInfo.channel}
                               onChange={e => changeMessageValue(e.target.value, "channel")}>
                    <Radio.Button value={0}>人工</Radio.Button>
                    <Radio.Button value={1}>延时</Radio.Button>
                    <Radio.Button value={2}>定时</Radio.Button>
                    <Radio.Button value={3}>按时发送</Radio.Button>
                  </Radio.Group>
                </Form.Item>
                {Channel}
                <Divider style={{width: "175%", marginLeft: -50}}/>
                <Form.Item>
                  <Button size="large" type="primary"
                          onClick={() => submitMessage(kefuMessageInfo, history, allCodes)}>确定{!kefuMessageInfo._id ? "并创建消息" : "并保存消息"}</Button>
                  {kefuMessageInfo._id && <Button style={{marginLeft: 20}} type="info" size="large"
                                                  onClick={() => history.goBack()}>取消编辑</Button>}
                  <Button size="large" style={{marginLeft: 30, background: "yellowgreen", borderColor: "yellowgreen"}}
                          type="primary" onClick={openPreviewModal}>发送预览</Button>
                </Form.Item>
              </Form>
            </div>
          </div>}/>
        </div>
        <Modal title="发送预览" visible={previewModal} onCancel={closePreviewModal}
               onOk={() => preview(kefuMessageInfo, openid)}>
          <Input onChange={changeOpenId} value={openid} placeholder="请输入预览用户的openid"/>
          <span className="tips">Tips: 在预览公号回复<span> openid </span>,将返回的内容复制到输入框中<br/>一个openid只对应一个公号</span>
        </Modal>
      </ConfigProvider>
    )
  }

  componentWillUnmount() {
    this.props.resetState()
  }
}

const mapState = state => ({
  kefuMessageInfo: state.message.kefu.kefuMessageInfo,
  content: state.message.kefu.content,
  allCodes: state.message.kefu.allCodes,
  previewModal: state.message.kefu.previewModal,
  openid: state.message.kefu.openid,
  allGroup: state.message.kefu.allGroup,
  groupInputVisible: state.message.kefu.groupInputVisible,
  groupValue: state.message.kefu.groupValue,
});

const mapDispatch = dispatch => ({
  initKefuMessageInfo(query) {
    dispatch(actionCreators.initKefuMessageInfo(query));
  },
  changeContentValue(e, name) {
    dispatch(actionCreators.changeContentValue(e.target.value, name));
  },
  uploadImg(kefuMessageInfo, info) {
    if (info.file.status === 'done') {
      let img = `http://t.dmmup.com/uploads/${info.file.response.filename}`,
        img_path = `./public/uploads/${info.file.response.filename}`;
      if (kefuMessageInfo.type === 0) {
        dispatch(actionCreators.changeContentValue(img, "picurl"));
      } else {
        dispatch(actionCreators.changeMessageValue(img, "img"));
        dispatch(actionCreators.changeMessageValue(img_path, "img_path"));
      }
    }
  },
  changeMessageValue(value, name) {
    dispatch(actionCreators.changeMessageValue(value, name));
  },
  resetState() {
    dispatch(actionCreators.resetState());
  },
  selectParams() {
    dispatch(actionCreators.selectParams());
  },
  submitMessage(info, history, allCodes) {
    dispatch(actionCreators.submitMessage(info, history, allCodes));
  },
  selectGonghao(codes) {
    dispatch(actionCreators.selectGonghao(codes))
  },
  deleteGonghao(codes, code) {
    codes.splice(codes.indexOf(code), 1);
    dispatch(actionCreators.selectGonghao(codes))
  },
  getAllCodes() {
    dispatch(actionCreators.getAllCodes())
  },
  changeOpenId(e) {
    dispatch(actionCreators.changeOpenId(e.target.value))
  },
  openPreviewModal() {
    dispatch(actionCreators.openPreviewModal())
  },
  closePreviewModal() {
    dispatch(actionCreators.closePreviewModal())
  },
  preview(info, openid) {
    dispatch(actionCreators.preview(info, openid))
  },
  getAllGroup() {
    dispatch(actionCreators.getAllGroup())
  },
  showGroupInput(that) {
    dispatch(actionCreators.showGroupInput(that))
  },
  handleGroupInputChange(e) {
    dispatch(actionCreators.handleGroupInputChange(e.target.value))
  },
  handleGroupInputConfirm(e) {
    dispatch(actionCreators.handleGroupInputConfirm(e.target.value))
  },
  handleClose(e, _id, index) {
    e.preventDefault();
    e.stopPropagation();
    dispatch(actionCreators.handleClose(_id, index))
  },
  cancelSelectGroup(e) {
    e.preventDefault();
    dispatch(actionCreators.cancelSelectGroup())
  }
});

export default connect(mapState, mapDispatch)(EditMessage);