import React, {Component} from 'react';
import {connect} from "react-redux";
import {Form, Button, Input, Radio, Transfer, Switch} from "antd";
import * as actionCreators from '../store/actionCreators';
import './index.scss';
import MenuReplyMessage from '../components/MenuReplyMessage';
class EditMenuInfo extends Component {

    componentDidMount() {
        let query = this.props.history.location.query;
        query && this.props.initMenuInfo(query);
    }

    render() {
        const {menuInfo, menuForm, sub_button, menuIndex, handleInputChange, handleSubInputChange, addFirstMenu, editMenu, submitMenu, addSecondMenu, editSecMenu, deleteFirMenu, deleteSubMenu, allCodes, targetKeys, changeGonghao, isSubmit, cancelMenu, handleInfoChange, handleIndividualChange, openModal, resetState, messageInfo, changeMessageInfo, uploadImg, addToList, updateMessageItem, updateCurrentMessage, deleteCurrentMessage, updateIndex} = this.props;
        const formItemLayout = {
            labelCol: {
                sm: {span: 4}
            },
            wrapperCol: {
                sm: {span: 16, offset: 0}
            },
            colon: false
        };
        const menuList = menuInfo["values"].map((item, index) => (
            <div className="menu" style={{width: menuInfo["values"].length === 1 ? "50%" : menuInfo["values"].length === 2 ? "33.33%" : "33.33%"}} key={index} onClick={() => editMenu(item, index)}>
                <span className="nav-title cursor">{item.name || "菜单名称"}</span>
                <ul className="sub-menu-list" style={{top: (item.sub_button && item.sub_button.length) ? (item.sub_button.length === 5 ?  - 4 * 46 - 55 : - item.sub_button.length * 46 - 55) : -55, display: menuIndex === index ? "block" : "none"}}>
                    {item.sub_button && item.sub_button.map((i, idx) => (
                        <li className="textEllipsis" key={idx} onClick={e=> editSecMenu(e, i, idx)}>{i.name || "子菜单名称"}</li>
                    ))}
                    <li className="add" style={{display: item.sub_button && item.sub_button.length < 5 ? "blcok" : "none"}} onClick={addSecondMenu}>+</li>
                    <div className="sanjiao sanjiao-out"/>
                    <div className="sanjiao sanjiao-in"/>
                </ul>
            </div>
        ));
        let keyOrUrl, subKeyOrUrl;
        if (sub_button.type === 'view') {
            subKeyOrUrl = <Form.Item label="链接">
                <Input value={sub_button.url} placeholder="请输入网页跳转链接，必须以 http 或 https 开头" onChange={(e) => handleSubInputChange(e, "url")}/>
            </Form.Item>
        } else if (sub_button.type === 'click') {
            subKeyOrUrl = <div className="content-wrapper">
                {messageInfo && <MenuReplyMessage messageInfo={messageInfo} changeMessageInfo={changeMessageInfo} uploadImg={uploadImg} addToList={addToList} updateMessageItem={updateMessageItem} updateCurrentMessage={updateCurrentMessage} deleteCurrentMessage={deleteCurrentMessage} updateIndex={updateIndex} />}
            </div>
        }
        if (menuForm.type === 'view') {
            keyOrUrl = <Form.Item label="链接">
                <Input value={menuForm.url} placeholder="请输入网页跳转链接，必须以 http 或 https 开头" onChange={(e) => handleInputChange(e, "url")}/>
            </Form.Item>
        } else if (menuForm.type === 'click') {
            keyOrUrl =
                <div className="content-wrapper">
                    <MenuReplyMessage messageInfo={messageInfo} changeMessageInfo={changeMessageInfo} uploadImg={uploadImg} addToList={addToList} updateMessageItem={updateMessageItem} updateCurrentMessage={updateCurrentMessage} deleteCurrentMessage={deleteCurrentMessage} updateIndex={updateIndex} />
                </div>
        }
        let menuInfoForm, submitButton;
        if (menuInfo.values.length !== 0) {
            if (menuForm.sub_button && menuForm.sub_button.length !== 0) {
                menuInfoForm =
                    <div className="menu-right fl">
                        <div className="menu-parent">
                            <div className="menu-title">
                                <h4 className="fl">菜单编辑</h4>
                                <Button className="fr" type="link" onClick={deleteFirMenu}>删除菜单</Button>
                            </div>
                            <Form {...formItemLayout}>
                                <Form.Item label="菜单名称">
                                    <Input value={menuForm.name} placeholder="一级菜单名称，仅支持中英文和数字，字数不超过4个" maxLength={4} allowClear
                                           onChange={(e) => handleInputChange(e, "name")}/>
                                </Form.Item>
                            </Form>
                            <div className="menu-children">
                                <div className="menu-title">
                                    <h4 className="fl">子菜单编辑</h4>
                                    <Button className="fr" type="link" onClick={deleteSubMenu}>删除子菜单</Button>
                                </div>
                                <Form {...formItemLayout}>
                                    <Form.Item label="子菜单名称">
                                        <Input value={sub_button.name} placeholder="二级菜单名称，仅支持中英文和数字，字数不超过8个" maxLength={8}
                                               onChange={(e) => handleSubInputChange(e, "name")}/>
                                    </Form.Item>
                                    <Form.Item label="子菜单内容">
                                        <Radio.Group value={sub_button.type} onChange={(e) => handleSubInputChange(e, "type")}>
                                            <Radio value="view">跳转网页</Radio>
                                            <Radio value="click">发送消息</Radio>
                                        </Radio.Group>
                                    </Form.Item>
                                    {subKeyOrUrl}
                                </Form>
                            </div>
                        </div>
                    </div>
            } else {
                menuInfoForm =
                    <div className="menu-right fl">
                        <div className="menu-parent">
                            <div className="menu-title">
                                <h4 className="fl">菜单编辑</h4>
                                <Button className="fr" type="link" onClick={deleteFirMenu}>删除菜单</Button>
                            </div>
                            <Form {...formItemLayout}>
                                <Form.Item label="菜单名称">
                                    <Input value={menuForm.name} maxLength={4} allowClear placeholder="一级菜单名称，仅支持中英文和数字，字数不超过4个" onChange={(e) => handleInputChange(e, "name")}/>
                                </Form.Item>
                                <Form.Item label="菜单内容">
                                    <Radio.Group value={menuForm.type} onChange={(e) => handleInputChange(e, "type")}>
                                        <Radio value="view">跳转网页</Radio>
                                        <Radio value="click">发送消息</Radio>
                                    </Radio.Group>
                                </Form.Item>
                                {keyOrUrl}
                            </Form>
                        </div>
                    </div>
            }
            submitButton = <div className="menu-right-btm fl">
                <Button type="primary" onClick={() => {openModal();this.props.getAllCodes()}
                }>提交</Button>
            </div>
        }
        let menuModal;
        if (isSubmit) {
            let individualSex;
            if (menuInfo.individual) {
                individualSex = <Form.Item label="性别">
                    <Radio.Group value={menuInfo.sex} onChange={e => handleInfoChange(e, "sex")}>
                        <Radio value="0">未知</Radio>
                        <Radio value="1">男</Radio>
                        <Radio value="2">女</Radio>
                    </Radio.Group>
                </Form.Item>
            }
            menuModal = <div className="menu-modal-wrapper">
                <div className="menu-modal">
                    <Transfer
                        dataSource={allCodes}
                        showSearch
                        targetKeys={targetKeys}
                        onChange={changeGonghao}
                        render={item => item.nick_name}
                        rowKey={record => record.code}
                        className="menu-transfer fl"
                    />
                    <Form className="fl" colon={false} labelCol={{sm: {span: 30}}} wrapperCol={{sm: {span: 30, offset: 0}}}
                          labelAlign="left">
                        <Form.Item label="标题">
                            <Input value={menuInfo.title} placeholder="请输入标题" onChange={(e) => handleInfoChange(e, "title")}/>
                        </Form.Item>
                        <Form.Item label="个性化菜单">
                            <Switch onChange={handleIndividualChange} checked={menuInfo.individual}/>
                        </Form.Item>
                        {individualSex}
                        <Form.Item label=" ">
                            <Button style={{marginRight: "10px"}} type="primary" onClick={() => submitMenu(this.props)}>提交</Button>
                            <Button onClick={cancelMenu}>取消</Button>
                        </Form.Item>
                    </Form>
                </div>
            </div>
        }
        return (
            <div>
                <div className='container edit-menu'>
                    <div className="title-wrapper">
                        <h2>菜单配置</h2>
                        <Button className="fr" onClick={() => resetState(this.props)}>返回上一级</Button>
                    </div>
                    <div className="menu-left fl">
                        <div className="menu-wrapper">
                            <div className="menu-cont">
                                <div className="menu-list">
                                    {menuList}
                                    <div className="menu add"
                                         style={menuList.length === 1 ? {width: "50%"} : (menuList.length === 2 ? {width: "33%"} : (menuList.length === 3 ? {display: "none"} : {width: "100%"}))}
                                         onClick={addFirstMenu}>
                                        <span className="nav-title cursor">+ 添加菜单</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    {menuInfoForm}
                    {submitButton}
                </div>
                {menuModal}
            </div>
        )
    }
    componentWillUnmount() {
        this.props.unMountState();
        this.props.resetMessageInfo();
    }

}

const mapState = state => ({
    menuInfo: state.gonghao.menu.menuInfo,
    menuForm: state.gonghao.menu.menuForm,
    sub_button: state.gonghao.menu.sub_button,
    menuIndex: state.gonghao.menu.menuIndex,
    allCodes: state.gonghao.menu.allCodes,
    isSubmit: state.gonghao.menu.isSubmit,
    targetKeys: state.gonghao.menu.targetKeys,
    messageInfo: state.gonghao.menu.messageInfo,
    updateIndex: state.gonghao.menu.updateIndex,
});

const mapDispatch = dispatch => ({
    initMenuInfo(record) {
        dispatch(actionCreators.initMenuInfo(record));
    },
    handleInfoChange(e, name) {
        dispatch(actionCreators.handleInfoChange(e.target.value, name));
    },
    handleIndividualChange(value) {
        dispatch(actionCreators.handleIndividualChange(value));
    },
    handleInputChange(e, name) {
        dispatch(actionCreators.handleInputChange(name, e.target.value))
    },
    handleSubInputChange(e, name) {
        dispatch(actionCreators.handleSubInputChange(name, e.target.value))
    },
    addFirstMenu() {
        dispatch(actionCreators.addFirstMenu())
    },
    editMenu(item, index) {
        dispatch(actionCreators.editMenu(item, index))
    },
    addSecondMenu(e) {
        e.stopPropagation();
        dispatch(actionCreators.addSecondMenu())
    },
    submitMenu(props) {
        dispatch(actionCreators.submitMenu(props))
    },
    openModal() {
        dispatch(actionCreators.openModal())
    },
    editSecMenu(e, data, idx) {
        e.stopPropagation();
        dispatch(actionCreators.editSecMenu(data, idx))
    },
    deleteFirMenu() {
        dispatch(actionCreators.deleteFirMenu())
    },
    deleteSubMenu() {
        dispatch(actionCreators.deleteSubMenu())
    },
    changeGonghao(targetKeys) {
        dispatch(actionCreators.changeGonghao(targetKeys))
    },
    getAllCodes() {
        dispatch(actionCreators.getAllCodes())
    },
    cancelMenu() {
        dispatch(actionCreators.cancelMenu())
    },
    resetState(props) {
        props.history.goBack();
        dispatch(actionCreators.resetState());
    },
    unMountState() {
        dispatch(actionCreators.resetState());
    },
    uploadImg(info) {
        if (info.file.status === 'done') {
            let picurl = `http://t.dmmup.com/uploads/${info.file.response.filename}`;
            dispatch(actionCreators.changeMessageInfo("picurl", picurl))
        }
    },
    changeMessageInfo(key, value) {
        dispatch(actionCreators.changeMessageInfo(key, value))
    },
    addToList() {
        dispatch(actionCreators.addToList())
    },
    updateMessageItem(item, index) {
        dispatch(actionCreators.updateMessageItem(item, index))
    },
    updateCurrentMessage() {
        dispatch(actionCreators.updateCurrentMessage())
    },
    deleteCurrentMessage() {
        dispatch(actionCreators.deleteCurrentMessage())
    },
    resetMessageInfo() {
        dispatch(actionCreators.resetState())
    },
});

export default connect(mapState, mapDispatch)(Form.create({name: 'editMenuInfo'})(EditMenuInfo));