import React, {Component} from 'react';
import {Avatar, Input, List, Layout, message, Select} from "antd";
import axios from 'axios';
import "./index.scss";

const {Sider, Content} = Layout;
const {Option} = Select;

class MultiSelectByGroup extends Component {

    state = {
        gonghaoTagList: [],
        gonghaoList: [],
        nick_name: "",
        group: "全部",
        codes: [],
        AllGonghaoList: []
    };

    UNSAFE_componentWillReceiveProps (nextProps) {
        this.setState({codes: nextProps.codes})
    }

    search = nick_name => {
        if (nick_name !== "") {
            axios.get("/conf/find_one", {params: {nick_name}})
                .then(res => {
                    if (res.data.data.length !== 0) {
                        const {data: gonghaoList} = res.data;
                        this.setState({gonghaoList})
                    } else {
                        message.info("没有查询到包含该字符的公号，请检查后重新输入")
                    }
                })
        } else {
            let {AllGonghaoList: gonghaoList} = this.state;
            this.setState({gonghaoList})
        }
    };

    changeSearchInput = e => {
        let nick_name = e.target.value;
        this.setState({nick_name, group: "全部"})
    };

    selectGonghao = gonghao => {
        let {codes} = this.state;
        const {code} = gonghao;
        const keyIndex = codes.indexOf(code);
        if(keyIndex > -1) {
            codes.splice(keyIndex, 1);
        } else {
            codes.push(code);
        }
        this.setState({codes});
        this.props.selectGonghao(codes);
    };

    handleChangeGroup = group => {
        const {AllGonghaoList: gonghaoList} = this.state;
        this.setState({group, nick_name: "", gonghaoList})
    };

    render() {
        const {gonghaoTagList, gonghaoList, nick_name, group, codes} = this.state;
        return (
            <Layout style={{background: "#fff"}} className="multi-gonghao-wrapper">
                <Sider className="message-sider-wrapper" theme="light" width="200">
                    <Select placeholder="按分组查询" value={group} className="search-input" onChange={this.handleChangeGroup}>
                        <Option value="全部">所有公号</Option>
                        {gonghaoTagList.map(item =>
                            <Option key={item._id} value={item.name}>{item.name}</Option>
                        )}
                    </Select>
                    <Input.Search
                        className="search-input"
                        placeholder="请输入公号名称"
                        onSearch={this.search}
                        value={nick_name}
                        allowClear
                        onChange={this.changeSearchInput}
                    />
                    <List className="gonghao-list"
                          dataSource={group !== "全部" ? gonghaoList.filter(i => i.group === group) : gonghaoList}
                          locale={{emptyText: <span style={{display: "none"}}>123</span>}}
                          renderItem={gonghao =>
                              <List.Item className={codes.indexOf(gonghao.code) > -1 ? "cursor selected" : "cursor"}
                                         onClick={() => this.selectGonghao(gonghao)} key={gonghao._id}>
                                  <Avatar shape="square" size={22} src={gonghao.head_img} />{gonghao.nick_name}
                              </List.Item>
                          }/>
                </Sider>
                <Content>{this.props.content}</Content>
            </Layout>
        )
    }

    getGonghaoTagList = () => {
        axios.get("/gonghaoTag")
            .then(res => {
                if (res.data.code === 1) {
                    const {data: gonghaoTagList} = res.data;
                    this.setState({gonghaoTagList})
                }
            })
    };

    getGonghaoList = () => {
        axios.get('/conf')
            .then(res => {
                const {data, code} = res.data;
                if (code === 1) {
                    this.setState({gonghaoList: data, AllGonghaoList: data})
                }
            })
    };

    componentDidMount() {
        this.getGonghaoTagList();
        this.getGonghaoList();
    }
}

export default MultiSelectByGroup;